<template>
    <div>
        <div class="b-title">vue数据驱动(数组/对象)</div>
        <div class="b-info">Vue是数据驱动视图。由于js的限制，vue不能检测对象属性的添加和删除。可以利用vue的set让修改内容的数组或者对象渲染到页面上。</div>
        <div class="b-info">this.$set(数组或者对象，修改的下标或者对象属性名，修改的值)。vue数组类型可通过下述方法触发视图更新，push,pop,shift,unshift,splice,sort,reverse。</div>
        <pre class="b-pre">
            <code lang="css">
      /*数组*/
      this.$set(this.arr,0,value);
      /*对象*/
      this.$set(this.obj,'key','value')
            </code>
        </pre>


    </div>
</template>

<script>
    export default {
        name: "v1"
    }
</script>

